.buttons {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    text-align: center;
    width: 90px;
    border: 1px black solid;
    border-radius: 110px;
    padding: 10px;
    background-color: rgb(210, 210, 210);
}

.button:active {
    background-color: rgb(169, 169, 169);
}